.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
  padding: 20px;
}

.App-header {
  background-color: white;
  height: 30vmin;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  font-size: calc(20px + 2vmin);
  color: #01344a;
}

.App-separator {
  background-color: #8dabbe;
  color: #01344a;
}
.App-link {
  color: #ea5a0b;
}

